/* –ì—Ä–∞–¥–∏–µ–Ω—Ç–Ω—ã–π —Ñ–æ–Ω –¥–ª—è –≤—Å–µ–π —Å—Ç—Ä–∞–Ω–∏—Ü—ã */
.password-reset-container {
   display: flex;
   justify-content: center;
   align-items: center;
   height: 100vh;
   background: linear-gradient(to right, #6a11cb, #2575fc);
   padding: 20px;
 }
 
 /* –ö–∞—Ä—Ç–æ—á–∫–∞ —Å —Ñ–æ—Ä–º–æ–π */
 .password-reset-card {
   background: white;
   padding: 30px;
   border-radius: 12px;
   box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
   text-align: center;
   width: 100%;
   max-width: 350px;
 }
 
 /* –ü–æ–ª—è –≤–≤–æ–¥–∞ */
 .password-reset-card input {
   width: 100%;
   padding: 12px;
   margin: 10px 0;
   border: 1px solid #ddd;
   border-radius: 8px;
   font-size: 16px;
 }
 
 /* –ö–Ω–æ–ø–∫–∞ (–∏—Å–ø–æ–ª—å–∑—É–µ—Ç—Å—è —Ç–æ—Ç –∂–µ –∫–ª–∞—Å—Å, —á—Ç–æ –∏ –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü–µ –≤—Ö–æ–¥–∞) */
 .auth-button {
   width: 100%;
   padding: 12px;
   background-color: #6a11cb;
   color: white;
   font-size: 16px;
   border: none;
   border-radius: 8px;
   cursor: pointer;
   transition: 0.3s;
 }
 
 .auth-button:hover {
   background-color: #2575fc;
 }
 
 /* –°–æ–æ–±—â–µ–Ω–∏—è –æ–± –æ—à–∏–±–∫–∞—Ö –∏–ª–∏ —É—Å–ø–µ—Ö–µ */
 .error-message {
   color: red;
   font-size: 14px;
   margin-top: 10px;
 }
 
 /* –î–æ–ø–æ–ª–Ω–∏—Ç–µ–ª—å–Ω–æ–µ –æ—Ñ–æ—Ä–º–ª–µ–Ω–∏–µ —Ç–µ–∫—Å—Ç–∞ */
 .password-reset-card p {
   font-size: 14px;
   margin-bottom: 10px;
 }
 
 /* –ê–¥–∞–ø—Ç–∏–≤–Ω—ã–µ —Å—Ç–∏–ª–∏ (–æ–ø—Ü–∏–æ–Ω–∞–ª—å–Ω–æ) */
 @media (max-width: 768px) {
   .password-reset-container {
     padding: 10px;
   }
 
   .password-reset-card {
     width: 90%;
     max-width: 400px;
     padding: 25px;
   }
 
   .password-reset-card input,
   .auth-button {
     font-size: 14px;
     padding: 10px;
   }
 }
 
 @media (max-width: 480px) {
   .password-reset-container {
     padding: 5px;
   }
 
   .password-reset-card {
     width: 100%;
     padding: 20px;
   }
 }
 