/* –ì—Ä–∞–¥–∏–µ–Ω—Ç–Ω—ã–π —Ñ–æ–Ω */
.auth-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: linear-gradient(to right, #6a11cb, #2575fc);
  padding: 20px;
}

/* –ö–∞—Ä—Ç–æ—á–∫–∞ —Å —Ñ–æ—Ä–º–æ–π */
.auth-card {
  background: white;
  padding: 30px;
  border-radius: 12px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
  text-align: center;
  width: 100%;
  max-width: 350px; /* –û–≥—Ä–∞–Ω–∏—á–∏–≤–∞–µ–º —à–∏—Ä–∏–Ω—É */
}

/* –ü–æ–ª—è –≤–≤–æ–¥–∞ */
.auth-card input {
  width: 100%;
  padding: 12px;
  margin: 10px 0;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 16px;
}

/* –ö–Ω–æ–ø–∫–∏ */
.auth-button {
  width: 100%;
  padding: 12px;
  background-color: #6a11cb;
  color: white;
  font-size: 16px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: 0.3s;
}

.auth-button:hover {
  background-color: #2575fc;
}

/* –ö–Ω–æ–ø–∫–∞ –ø–µ—Ä–µ–∫–ª—é—á–µ–Ω–∏—è */
.switch-button {
  background: none;
  color: #6a11cb;
  font-size: 14px;
  border: none;
  cursor: pointer;
  margin-top: 10px;
}

.switch-button:hover {
  color: #2575fc;
}

/* –°–æ–æ–±—â–µ–Ω–∏—è –æ–± –æ—à–∏–±–∫–∞—Ö */
.error-message {
  color: red;
  font-size: 14px;
}

/* üîπ –ê–¥–∞–ø—Ç–∞—Ü–∏—è –ø–æ–¥ –ø–ª–∞–Ω—à–µ—Ç—ã (‚â§ 768px) */
@media (max-width: 768px) {
  .auth-container {
    padding: 10px;
  }

  .auth-card {
    width: 90%;
    max-width: 400px;
    padding: 25px;
  }

  .auth-card input {
    font-size: 14px;
    padding: 10px;
  }

  .auth-button {
    font-size: 14px;
    padding: 10px;
  }

  .switch-button {
    font-size: 13px;
  }
}

/* üîπ –ê–¥–∞–ø—Ç–∞—Ü–∏—è –ø–æ–¥ –º–æ–±–∏–ª—å–Ω—ã–µ —É—Å—Ç—Ä–æ–π—Å—Ç–≤–∞ (‚â§ 480px) */
@media (max-width: 480px) {
  .auth-container {
    padding: 5px;
  }

  .auth-card {
    width: 100%;
    padding: 20px;
  }

  .auth-card h2 {
    font-size: 20px;
  }

  .auth-card input {
    font-size: 14px;
    padding: 8px;
  }

  .auth-button {
    font-size: 14px;
    padding: 10px;
  }

  .switch-button {
    font-size: 12px;
  }
}